import React from "react";
import resume from "./data/Shashank Nathe Resume.pdf";
import {motion} from 'framer-motion'

const Contact = () => {
  return (
    <motion.section 
    initial={{ opacity: 0 }}
    whileInView={{ opacity: 1 }}
    transition={{ duration: 1 }}
    className="contact" id="contact">
      <div>
        <h3>Contact Me</h3>
        <div className="contactinfo">
          <h6>+918483851903</h6>
          <h6>shashanknathe@gmail.com</h6>
        </div>

        <a href={resume} target="_blank">
          <button className="dark-btn">Resume</button>
        </a>
      </div>
      <form
        action="https://formsubmit.co/9d5b7bc66ee3d14d3ff6a536cc8f56b2"
        method="POST"
      >
        <input type="hidden" name="_captcha" value="false" />
        <input
          type="hidden"
          name="_next"
          value="https://ShashankNathe.github.io/Portfolio/#/thankyou"
        />
        <input type="text" name="name" placeholder="Name" required />
        <input type="email" name="email" placeholder="Email" required />
        <textarea
          name="message"
          placeholder="Enter your message"
          required
        ></textarea>
        <button type="submit">Send</button>
      </form>
    </motion.section>
  );
};

export default Contact;
