import React from "react";
import dashboard from "./Images/dashboard.png";
import ecom from "./Images/ecommerce.png";
import socialmedia from "./Images/screenshot.png";
import {motion} from 'framer-motion'

const Projects = () => {
  return (
    <motion.section 
    initial={{ opacity: 0 }}
    whileInView={{ opacity: 1 }}
    transition={{ duration: 1 }}
    className="project" id="project">
      <h3>Projects</h3>

      <a href="https://shashanknathe.github.io/Sunkart/" target="_blank">
        <div className="cards  animationRight">
          <div className="pinfo">
            <h3>Ecommerce Website</h3>
            <p>
              a sunglasses ecommerce website made in <span>React</span>{" "}
            </p>
          </div>
          <div className="img">
            <img src={ecom} alt="sunkart" />
          </div>
        </div>
      </a>

      <a href="https://shashanknathe.github.io/Dashboard/" target="_blank">
        <div className="cards animationLeft">
          <div className="img dash">
            <img src={dashboard} alt="dashboard" />
          </div>
          <div className="pinfo">
            <h3>Stock Market Dashboard</h3>
            <p>
              a simple dashboard to track the current market, its news and much
              more. Created with <span>React</span>
            </p>
          </div>
        </div>
      </a>

      <div className="cards animationRight">
        <div className="pinfo">
          <h3>Social Media</h3>
          <p>
            A social media page made in <span>React</span>
          </p>
        </div>
        <div className="img">
          <img src={socialmedia} alt="socialmedia" />
        </div>
      </div>
    </motion.section>
  );
};

export default Projects;
