import React from "react";
import {motion} from 'framer-motion';
import js from "./Images/js.png";
import html from "./Images/HTML.png";
import css from "./Images/css.png";
import react from "./Images/React.png";
import python from "./Images/python.png";
import git from "./Images/git.png";
const About = () => {
  return (
    <motion.section 
    initial={{ opacity: 0 }}
    whileInView={{ opacity: 1 }}
    transition={{ duration: 1 }}
    className="about" id="about">
      <div className="animationLeft">
        <h2> About Me</h2>
        <p>
          i am a cs graduate with an interest towards making beautiful and
          useful responsive websites
        </p>
      </div>
      <div className="skills animationRight">
        <h3>Skills</h3>

        <section className="art animationLeft">
          <img src={git} alt="git" />
          <div className="flexcontainer">
            <div className="circle1">
              <img src={html} alt="HTML" />
            </div>
            <div className="circle2">
              <img src={css} alt="CSS" />
            </div>
            <div className="circle3">
              <img src={js} alt="javascript" />
            </div>
            <div className="circle3">
              <img src={python} alt="python" />
            </div>
            <div className="circle4">
              <img src={react} alt="react" className="reactlogo" />
            </div>
          </div>
        </section>
      </div>
    </motion.section>
  );
};

export default About;
