import React from "react";
import { motion} from "framer-motion"

const Main = () => {
  return (
    // <AnimatePresence>
    <motion.main 
    initial={{ opacity: 0 }}
    whileInView={{ opacity: 1 }}
    transition={{ duration: 1 }}
    className="intro" id='home'>
      <section className="root">
        <div className="flexcontaine">
          <div className="circle11">
            <div className="circle12">
              <div className="circle13">
                <div className="circle14">
                  <div>
                    &lt;<span>/</span>&gt;
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>

      <section className="info">
        <p>$&#123;Hello!&#125;</p>
        <h3>I am Shashank,</h3>
        <h4>a front-end developer</h4>
      </section>
    </motion.main>
    // </AnimatePresence>
  );
};

export default Main;
