import React from "react";
import { Link } from "react-router-dom";



const Thankyou = () => {

  return (    <main className="intro" id='home'>
  <section className="root">
    <div className="flexcontaine">
      <div className="circle11">
        <div className="circle12">
          <div className="circle13">
            <div className="circle14">
              <div>
                &lt;<span>/</span>&gt;
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>

  <section className="info  animationRight">
    <h3>Thankyou</h3>
    <Link to={'/'}>
    <button className="dark-btn">Go Back</button>
    </Link>
  </section>
</main>
  );
};

export default Thankyou;
